import axios from "axios";

// const baseURL = "https://tldr-with-friends.herokuapp.com/api/";
const baseURL = "http://localhost:8000/api/";

const axiosInstance = axios.create({
    baseURL: baseURL,
    timeout: 3000000,
    headers: {
        Authorization: localStorage.getItem("access_token")
            ? "JWT " + localStorage.getItem("access_token")
            : null,
        "Content-Type": "application/json",
        accept: "application/json",
    },
});

// Adapted from Very Academy
axiosInstance.interceptors.response.use(
    (response) => {
        return response;
    },
    async function (error) {
        const originalRequest = error.config;

        // If server isn't working
        if (typeof error.response === "undefined") {
            alert(
                "A server/network error occurred. " +
                    "Looks like CORS might be the problem. " +
                    "Sorry about this - we will get it fixed shortly."
            );
            return Promise.reject(error);
        }

        // if (window.location.href === "/") {
        if (error.response.status === 404) {
            alert("Please Input A Non-Paywalled Article/Non-PDF URL");
            return Promise.reject(error);
        }

        if (error.response.status === 401) {
            alert("Please Check Username/Password");
            return Promise.reject(error);
        }

        if (error.response.status === 505) {
            alert("Please Enter A Valid URL");
            return Promise.reject(error);
        }

        if (error.response.status === 400) {
            alert("Username/Email Taken");
            return Promise.reject(error);
        }
        // }

        // Prevents looping of creating refresh tokens
        if (
            error.response.status === 401 &&
            originalRequest.url === baseURL + "token/refresh/"
        ) {
            window.location.href = "/";
            return Promise.reject(error);
        }

        if (
            error.response.data.code === "token_not_valid" &&
            error.response.status === 401 &&
            error.response.statusText === "Unauthorized"
        ) {
            const refreshToken = localStorage.getItem("refresh_token");

            if (refreshToken) {
                const tokenParts = JSON.parse(atob(refreshToken.split(".")[1]));

                // exp date in token is expressed in seconds, while now() returns milliseconds:
                const now = Math.ceil(Date.now() / 1000);
                console.log(tokenParts.exp);

                if (tokenParts.exp > now) {
                    return axiosInstance
                        .post("/token/refresh/", { refresh: refreshToken })
                        .then((response) => {
                            localStorage.setItem(
                                "access_token",
                                response.data.access
                            );
                            localStorage.setItem(
                                "refresh_token",
                                response.data.refresh
                            );

                            axiosInstance.defaults.headers["Authorization"] =
                                "JWT " + response.data.access;
                            originalRequest.headers["Authorization"] =
                                "JWT " + response.data.access;

                            return axiosInstance(originalRequest);
                        })
                        .catch((err) => {
                            console.log(err);
                        });
                } else {
                    console.log(
                        "Refresh token is expired",
                        tokenParts.exp,
                        now
                    );
                    window.location.href = "/login/";
                }
            } else {
                console.log("Refresh token not available.");
                window.location.href = "/login/";
            }
        }

        // specific error handling done elsewhere
        return Promise.reject(error);
    }
);

export default axiosInstance;
